@charset "utf-8";

.store-cont {
  .store-search {
    width: 85.068vw;
    background: #fff;
    padding: 7.466vw;
    select {
      background: #fff url(/_ui/img/m/icn/icn-arrow02.png) no-repeat 99% 50%;
      background-size: 3vw;
      font-size: 3.733vw;
      line-height: 10.8vw;
      border-bottom: .266vw #eee solid;
    }
    input {
      width: 100%;
      border-bottom: .266vw #eee solid;
      line-height: 10.8vw;
      font-size: 3.733vw;
    }
    button {
      display: block;
      width: 100%;
      padding: 2.4vw 0 3.066vw;
      color: #1f1f1f;
      font-size: 4.266vw;
      text-align: center;
      border: .266vw solid #f1f1f1;
    }
    select, input {
      margin-bottom: 4vw;
    }
  }
  .store-search-result {
    background-color: #f3f4f7;
    .title {
      padding: 10.8vw 7.466vw 1.066vw;
      font-size: 4vw;
      line-height: 5.333vw;
      strong {
        font-weight: bold;
      }
      select {
        float: right;
        width: 18.133vw;
        padding-right: 4vw;
        background: url(/_ui/img/m/icn/icn-arrow04.png) no-repeat 99% 50%;
        background-size: 3vw;
        font-size: 3.466vw;
        line-height: 5.333vw;
      }
    }
    .list-non {
      padding: 51.33vw 0;
      font-size: 4.26vw;
      color: #aaa;
      background: #f3f4f7;
      text-align: center;
    }
    ul.store-list {
      padding: 3.6vw;
      font-size: 3.466vw;
      &>li {
        position: relative;
        padding: 4.533vw;
        margin-bottom: 2px;
        background-color: #fff;
        .type {
          color: #aaa;
        }
        .name {
          font-size: 4vw;
          margin: 3.866vw 0 2.666vw;
          word-break: keep-all;
        }
        .address {
          max-width: 64vw;
          color: #898989;
          word-break: keep-all;
          margin-top: 4.666vw;
        }
        a {
          position: absolute;
          right: 4.533vw;
          bottom: 4.533vw;
          padding: 1.466vw 1.733vw;
          border: 0.266vw solid #f1f1f1;
        }
      }
    }
    .more {
      width: 7.733vw;
      height: 7.733vw;
      display: block;
      background: url(/_ui/img/m/icn/icn-more.png) no-repeat;
      background-size: contain;
      font-size: 0;
      text-indent: -999px;
      margin: 6.666vw auto 17.333vw;
    }
    &::after {
      content: '';
      display: table;
      clear: both;
    }
  }
}

@media (min-width: 750px) {
  .store-cont {
    .store-search {
      width: 638px;
      padding: 56px;
      select {
        background-size: 22px;
        font-size: 28px;
        line-height: 181px;
        border-bottom: 2px #eee solid;
      }
      input {
        border-bottom: 2px #eee solid;
        line-height: 81px;
        font-size: 28px;
      }
      button {
        padding: 18px 0 23px;
        font-size: 32px;
        border: 2px #eee solid;
      }
      select, input {
        margin-bottom: 30px;
      }
    }
    .store-search-result {
      .title {
        padding: 81px 56px 8px;
        font-size: 30px;
        line-height: 40px;
        select {
          width: 136px;
          padding-right: 30px;
          background-size: 22px;
          font-size: 26px;
          line-height: 40px;
        }
      }
      .list-non {
        padding: 257px 0;
        font-size: 32px;
      }
      ul.store-list {
        padding: 27px;
        font-size: 26px;
        &>li {
          padding: 34px;
          .name {
            font-size: 30px;
            margin: 29px 0 20px;
          }
          .address {
            max-width: 480px;
            margin-top: 35px;
          }
          a {
            right: 34px;
            bottom: 34px;
            padding: 11px 13px;
            border: 2px #eee solid;
          }
        }
      }
      .more {
        width: 58px;
        height: 58px;
        margin: 50px auto 130px;
      }
    }
  }
}
